import Vue from 'vue';
import Router from 'vue-router';
import TrsList from './views/TrsList.vue';
import TrsDetail from './views/TrsDetail.vue';
import Navigation from './components/Navigation.vue';
import FileMenu from './components/FileMenu.vue';
import PrintPreview from './components/PrintPreview.vue';

Vue.use(Router);

function cast(strId) {
  const id = parseInt(strId, 10);
  if (Number.isNaN(id)) {
    return 0;
  }
  return id;
}

function detailProps(route) {
  return { id: cast(route.params.id), cloned: false };
}

function clonedProps(route) {
  return { id: cast(route.params.id), cloned: true };
}

export default new Router({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'list',
      components: {
        default: TrsList,
        context: FileMenu,
      },
      props: {
        default: null,
      },
    },
    {
      path: '/detail/:id',
      name: 'detail',
      components: {
        default: TrsDetail,
        navigation: Navigation,
        context: PrintPreview,
      },
      props: {
        default: detailProps,
        context: detailProps,
        navigation: { icon: 'arrow_back' },
      },
    },
    {
      path: '/cloned/:id',
      name: 'cloned',
      components: {
        default: TrsDetail,
        navigation: Navigation,
        context: PrintPreview,
      },
      props: {
        default: clonedProps,
        context: clonedProps,
        navigation: { icon: 'arrow_back' },
      },
    },
  ],
  scrollBehavior(to) {
    if (to.name === 'list') {
      return {
        selector: '#selected',
        offset: { x: 0, y: 100 },
      };
    }
    return { x: 0, y: 0 };
  },
});
